@import '../libs/reset.scss/reset.scss';
@import 'partials/fonts';
@import 'partials/variables';
@import 'partials/mixins';
@import '../libs/slick/slick/slick.scss';
// @import '../../../build/assets/i/svg/bg/css/sprite.css'

html,
body {
  font-size: 17px;
}

body {
  margin: 0;
  padding: 0;
  @include helvetica;
  font-size: 16px;
  line-height: 1.2;
  color: #000;
  background: white;
  @media screen and (max-width: 800px) {
    padding-bottom: 90px;
  }

}

* {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
}

ul {
  @include reset-list;
}

a {
  text-decoration: none;
  color: inherit;

  &:visited {
    color: inherit;
  }
}

p {
  margin: 0;
  padding: 0;
}

.hidden {
  display: none;
}

.container {
  margin: 0 auto;
  width: 1200px;
}

.content {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
}

